import React, { useState, useEffect, useRef } from 'react';
import voiceService, { VoiceNote } from '../../services/voiceService';
import taskMatchingService, { ActionItemSuggestion } from '../../services/taskMatchingService';
import TaskAssignmentSuggestions from './TaskAssignmentSuggestions';
import { useSelector, useDispatch } from 'react-redux';
import { RootState, AppDispatch } from '../../store';
import { Task } from '../../types';
import { updateTask, createTask } from '../../store/slices/tasksSlice';
import {
  MicrophoneIcon,
  StopIcon,
  PlayIcon,
  PauseIcon,
  TrashIcon,
  CheckIcon,
  ExclamationTriangleIcon,
  ClockIcon,
  LightBulbIcon,
  FlagIcon,
  DocumentTextIcon,
} from '@heroicons/react/24/outline';

interface VoiceRecorderProps {
  entityType: 'project' | 'task';
  entityId: string;
  entityName: string;
  onNotesExtracted?: (notes: VoiceNote[]) => void;
  onClose?: () => void;
}

const VoiceRecorder: React.FC<VoiceRecorderProps> = ({
  entityType,
  entityId,
  entityName,
  onNotesExtracted,
  onClose
}) => {
  const [isRecording, setIsRecording] = useState(false);
  const [isProcessing, setIsProcessing] = useState(false);
  const [audioBlob, setAudioBlob] = useState<Blob | null>(null);
  const [transcription, setTranscription] = useState('');
  const [extractedNotes, setExtractedNotes] = useState<VoiceNote[]>([]);
  const [recordingTime, setRecordingTime] = useState(0);
  const [isPlaying, setIsPlaying] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [hasPermission, setHasPermission] = useState<boolean | null>(null);
  const [showTaskSuggestions, setShowTaskSuggestions] = useState(false);
  const [taskSuggestions, setTaskSuggestions] = useState<ActionItemSuggestion[]>([]);
  const [hasWakeLockSupport, setHasWakeLockSupport] = useState(false);

  // Get project tasks and team members for task matching
  const { tasks } = useSelector((state: RootState) => state.tasks);
  const { currentProject } = useSelector((state: RootState) => state.projects);
  const dispatch = useDispatch<AppDispatch>();
  
  // Debug: Log the current state
  console.log('VoiceRecorder current state:', {
    tasksCount: tasks?.length || 0,
    currentProject: currentProject?.name || 'No project',
    projectId: currentProject?.id || 'No ID',
    entityType,
    entityId
  });

  const audioRef = useRef<HTMLAudioElement>(null);
  const intervalRef = useRef<NodeJS.Timeout | null>(null);

  useEffect(() => {
    // Check microphone permission on component mount
    checkMicrophonePermission();
    
    // Check wake lock support
    setHasWakeLockSupport('wakeLock' in navigator);
    
    // Debug: Log initial state on mount
    console.log('VoiceRecorder mounted:', {
      entityType,
      entityId,
      tasksAvailable: tasks?.length || 0,
      currentProject: currentProject?.name || null,
      wakeLockSupported: 'wakeLock' in navigator
    });
    
    return () => {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
      }
    };
  }, [entityType, entityId, tasks?.length, currentProject?.name]);

  const checkMicrophonePermission = async () => {
    if (!voiceService.isSupported()) {
      setError('Voice recording is not supported in this browser');
      setHasPermission(false);
      return;
    }

    const permission = await voiceService.requestPermission();
    setHasPermission(permission);
    if (!permission) {
      setError('Microphone permission is required for voice recording');
    }
  };

  const startRecording = async () => {
    try {
      setError(null);
      await voiceService.startRecording();
      setIsRecording(true);
      setRecordingTime(0);
      
      // Start timer
      intervalRef.current = setInterval(() => {
        setRecordingTime(prev => prev + 1);
      }, 1000);
    } catch (error) {
      console.error('Failed to start recording:', error);
      setError('Failed to start recording. Please check your microphone.');
    }
  };

  const stopRecording = async () => {
    try {
      if (intervalRef.current) {
        clearInterval(intervalRef.current);
        intervalRef.current = null;
      }

      const blob = await voiceService.stopRecording();
      setIsRecording(false);
      setAudioBlob(blob);
      
      // Start processing
      setIsProcessing(true);
      await processRecording(blob);
    } catch (error) {
      console.error('Failed to stop recording:', error);
      setError('Failed to stop recording');
      setIsRecording(false);
    }
  };

  const processRecording = async (blob: Blob) => {
    try {
      setError(null); // Clear any previous errors
      
      // Transcribe audio
      const transcriptionText = await voiceService.transcribeAudio(blob);
      setTranscription(transcriptionText);

      // Extract intelligent notes
      const notes = voiceService.extractIntelligentNotes(transcriptionText);
      setExtractedNotes(notes);

      // Generate task assignment suggestions if we're at project level and have action items
      console.log('Checking for smart assignment:', {
        entityType,
        tasksLength: tasks?.length || 0,
        notesLength: notes.length,
        actionItems: notes.filter(note => note.type === 'action').length,
        currentProject: !!currentProject,
        projectId: entityId
      });
      
      if (entityType === 'project' && tasks && tasks.length > 0) {
        const actionItems = notes.filter(note => note.type === 'action');
        console.log('Action items found:', actionItems.length, actionItems);
        
        if (actionItems.length > 0) {
          const teamMembers = currentProject?.members?.map(m => m.user).filter(Boolean) || [];
          console.log('Team members:', teamMembers.length, teamMembers);
          
          try {
            const suggestions = taskMatchingService.analyzeAndSuggestAssignments(
              notes,
              tasks,
              teamMembers as any[]
            );
            
            console.log('Generated suggestions:', suggestions.length, suggestions);
            
            if (suggestions.length > 0) {
              setTaskSuggestions(suggestions);
              console.log('Task suggestions set successfully');
              // Don't show suggestions immediately, wait for user to save notes first
            } else {
              console.log('No suggestions generated - empty result');
            }
          } catch (error) {
            console.error('Error generating task suggestions:', error);
          }
        } else {
          console.log('No action items found in notes');
        }
      } else {
        console.log('Conditions not met for smart assignment:', {
          isProject: entityType === 'project',
          hasTasks: tasks && tasks.length > 0,
          tasksArray: tasks
        });
      }

      setIsProcessing(false);
    } catch (error: any) {
      console.error('Failed to process recording:', error);
      
      // Show the specific error message from the service
      const errorMessage = error.message || 'Failed to process recording. Please try again.';
      setError(errorMessage);
      setIsProcessing(false);
    }
  };

  const playRecording = () => {
    if (audioBlob && audioRef.current) {
      if (isPlaying) {
        audioRef.current.pause();
        setIsPlaying(false);
      } else {
        try {
          // Use a safer approach for creating audio URL
          let audioUrl: string;
          
          // Try URL.createObjectURL first, fallback to FileReader if blocked
          try {
            audioUrl = URL.createObjectURL(audioBlob);
          } catch (error) {
            console.warn('URL.createObjectURL blocked, using FileReader fallback:', error);
            // Use FileReader as fallback
            const reader = new FileReader();
            reader.onload = (e) => {
              if (e.target?.result && audioRef.current) {
                audioRef.current.src = e.target.result as string;
                audioRef.current.play().catch(console.error);
                setIsPlaying(true);
                
                audioRef.current.onended = () => {
                  setIsPlaying(false);
                };
              }
            };
            reader.readAsDataURL(audioBlob);
            return;
          }
          
          audioRef.current.src = audioUrl;
          audioRef.current.play().catch(console.error);
          setIsPlaying(true);

          audioRef.current.onended = () => {
            setIsPlaying(false);
            try {
              URL.revokeObjectURL(audioUrl);
            } catch (error) {
              console.warn('Failed to revoke object URL:', error);
            }
          };
        } catch (error) {
          console.error('Failed to play recording:', error);
          setError('Failed to play recording. This may be due to browser security restrictions.');
        }
      }
    }
  };

  const deleteRecording = () => {
    setAudioBlob(null);
    setTranscription('');
    setExtractedNotes([]);
    setRecordingTime(0);
    setIsPlaying(false);
    setError(null);
  };

  const saveNotes = async () => {
    try {
      setIsProcessing(true);
      console.log('Saving notes...', {
        extractedNotes: extractedNotes.length,
        transcriptionLength: transcription.length,
        entityType,
        entityId
      });
      
      await voiceService.saveVoiceNote(entityType, entityId, extractedNotes, transcription);
      console.log('Notes saved successfully');
      
      if (onNotesExtracted) {
        onNotesExtracted(extractedNotes);
      }
      
      setIsProcessing(false);
      
      // Show task suggestions if we have them
      console.log('After saving notes, checking suggestions:', {
        suggestionsLength: taskSuggestions.length,
        extractedNotesCount: extractedNotes.length,
        actionItemsCount: extractedNotes.filter(n => n.type === 'action').length,
        suggestions: taskSuggestions
      });
      
      if (taskSuggestions.length > 0) {
        console.log('Showing task suggestions modal');
        setShowTaskSuggestions(true);
      } else {
        console.log('No suggestions to show - reasons could be:', {
          noTaskSuggestions: taskSuggestions.length === 0,
          noActionItems: extractedNotes.filter(n => n.type === 'action').length === 0,
          entityType: entityType,
          tasksAvailable: tasks?.length || 0
        });
        // Close if no suggestions
        if (onClose) {
          onClose();
        }
      }
    } catch (error) {
      console.error('Failed to save notes:', error);
      setError('Failed to save notes');
      setIsProcessing(false);
    }
  };

  // Handler for assigning action item to existing task
  const handleAssignToTask = async (actionItemId: string, taskId: string, updates?: Partial<Task>) => {
    try {
      setIsProcessing(true);
      
      // Find the action item content
      const actionItem = taskSuggestions.find(s => s.actionItem.timestamp === actionItemId)?.actionItem;
      if (!actionItem) return;
      
      // Find the existing task
      const existingTask = tasks.find(t => t.id === taskId);
      if (!existingTask) return;
      
      // Prepare the update: add the action item to task description
      const updatedDescription = existingTask.description 
        ? `${existingTask.description}\n\n**Action from Voice Note:**\n- ${actionItem.content}`
        : `**Action from Voice Note:**\n- ${actionItem.content}`;
      
      // Merge with any suggested updates (assignee, deadline, priority)
      const taskUpdates = {
        ...updates,
        description: updatedDescription
      };
      
      // Dispatch the update
      await dispatch(updateTask({ id: taskId, updates: taskUpdates })).unwrap();
      
      // Remove the suggestion that was just processed
      setTaskSuggestions(prev => 
        prev.filter(suggestion => suggestion.actionItem.timestamp !== actionItemId)
      );
      
      setIsProcessing(false);
      
      // Close suggestions if no more suggestions
      if (taskSuggestions.length <= 1) {
        setShowTaskSuggestions(false);
        if (onClose) {
          onClose();
        }
      }
    } catch (error) {
      console.error('Failed to assign to task:', error);
      setError('Failed to assign action item to task');
      setIsProcessing(false);
    }
  };

  // Handler for creating new task from action item
  const handleCreateNewTask = async (actionItemId: string, taskTitle: string, taskData: Partial<Task>) => {
    try {
      setIsProcessing(true);
      
      // Ensure we have a project ID for creating the task
      const projectId = entityType === 'project' ? entityId : currentProject?.id;
      if (!projectId) {
        throw new Error('No project ID available for creating task');
      }
      
      // Create the task with the action item as description
      const newTaskData = {
        ...taskData,
        title: taskTitle,
        project_id: projectId
      };
      
      // Dispatch the create action
      await dispatch(createTask(newTaskData as any)).unwrap();
      
      // Remove the suggestion that was just processed
      setTaskSuggestions(prev => 
        prev.filter(suggestion => suggestion.actionItem.timestamp !== actionItemId)
      );
      
      setIsProcessing(false);
      
      // Close suggestions if no more suggestions
      if (taskSuggestions.length <= 1) {
        setShowTaskSuggestions(false);
        if (onClose) {
          onClose();
        }
      }
    } catch (error) {
      console.error('Failed to create new task:', error);
      setError('Failed to create new task');
      setIsProcessing(false);
    }
  };

  // Handler for skipping action item
  const handleSkipAction = (actionItemId: string) => {
    // Remove the suggestion that was skipped
    setTaskSuggestions(prev => 
      prev.filter(suggestion => suggestion.actionItem.timestamp !== actionItemId)
    );
    
    // Close suggestions if no more suggestions
    if (taskSuggestions.length <= 1) {
      setShowTaskSuggestions(false);
      if (onClose) {
        onClose();
      }
    }
  };

  // Handler for closing suggestions
  const handleCloseSuggestions = () => {
    setShowTaskSuggestions(false);
    if (onClose) {
      onClose();
    }
  };

  const formatTime = (seconds: number): string => {
    const mins = Math.floor(seconds / 60);
    const secs = seconds % 60;
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  const getNoteIcon = (type: string) => {
    switch (type) {
      case 'action':
        return <CheckIcon className="w-4 h-4 text-blue-500" />;
      case 'decision':
        return <FlagIcon className="w-4 h-4 text-green-500" />;
      case 'issue':
        return <ExclamationTriangleIcon className="w-4 h-4 text-red-500" />;
      case 'idea':
        return <LightBulbIcon className="w-4 h-4 text-yellow-500" />;
      default:
        return <DocumentTextIcon className="w-4 h-4 text-gray-500" />;
    }
  };

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high':
        return 'bg-red-100 text-red-800 border-red-200';
      case 'medium':
        return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      case 'low':
        return 'bg-green-100 text-green-800 border-green-200';
      default:
        return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  if (hasPermission === false) {
    return (
      <div className="card max-w-md mx-auto text-center">
        <div className="mb-4">
          <ExclamationTriangleIcon className="w-12 h-12 text-red-500 mx-auto mb-4" />
          <h3 className="text-lg font-semibold text-gray-900 mb-2">Microphone Access Required</h3>
          <p className="text-gray-600">{error}</p>
        </div>
        <div className="space-y-3">
          <button onClick={checkMicrophonePermission} className="btn-primary w-full">
            Request Permission
          </button>
          {onClose && (
            <button onClick={onClose} className="btn-secondary w-full">
              Cancel
            </button>
          )}
        </div>
      </div>
    );
  }

  return (
    <div className="card max-w-2xl mx-auto">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h3 className="text-lg font-semibold text-gray-900">AI Voice Notes</h3>
          <p className="text-sm text-gray-600">
            Record voice notes for {entityType}: <span className="font-medium">{entityName}</span>
          </p>
        </div>
        {onClose && (
          <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
            
          </button>
        )}
      </div>

      {/* Recording Controls */}
      <div className="space-y-6">
        {/* Recording Status */}
        <div className="text-center">
          {isRecording && (
            <>
              <div className="flex items-center justify-center gap-2 mb-4">
                <div className="w-3 h-3 bg-red-500 rounded-full animate-pulse"></div>
                <span className="text-sm font-medium text-red-600">Recording...</span>
              </div>
              <div className="bg-amber-50 border border-amber-200 rounded-lg p-3 mb-4 mx-auto max-w-md">
                {hasWakeLockSupport ? (
                  <p className="text-sm text-amber-800">
                    <strong> Screen Lock Protection Active</strong><br/>
                    Your screen will stay on during recording. Recording continues even if you switch apps.
                  </p>
                ) : (
                  <p className="text-sm text-amber-800">
                    <strong> Screen May Turn Off</strong><br/>
                    Your device doesn't support screen lock. Please keep the screen on manually during recording.
                  </p>
                )}
                {recordingTime > 300 && ( // Show after 5 minutes
                  <p className="text-xs text-amber-700 mt-2">
                     Tip: Keep this tab open for best results. Some browsers may pause recording if you navigate away.
                  </p>
                )}
              </div>
            </>
          )}
          
          <div className="flex items-center justify-center gap-2 mb-4">
            <ClockIcon className="w-4 h-4 text-gray-500" />
            <span className="text-lg font-mono">{formatTime(recordingTime)}</span>
            {recordingTime > 1800 && ( // Show warning after 30 minutes
              <span className="text-sm text-amber-600 ml-2">
                (Long recording - may take time to process)
              </span>
            )}
          </div>
          
          {/* Recording Stats */}
          {(recordingTime > 0 || audioBlob) && (
            <div className="text-center text-sm text-gray-500 mb-4">
              {audioBlob && (
                <span>File size: {(audioBlob.size / (1024 * 1024)).toFixed(1)}MB</span>
              )}
              {recordingTime > 3600 && (
                <div className="text-amber-600 mt-1">
                   Very long recording - processing may take several minutes
                </div>
              )}
            </div>
          )}

          {/* Control Buttons */}
          <div className="flex justify-center gap-4">
            {!isRecording ? (
              <button
                onClick={startRecording}
                disabled={isProcessing || !hasPermission}
                className="flex items-center gap-2 bg-red-500 hover:bg-red-600 text-white px-6 py-3 rounded-lg font-medium disabled:opacity-50"
              >
                <MicrophoneIcon className="w-5 h-5" />
                Start Recording
              </button>
            ) : (
              <button
                onClick={stopRecording}
                className="flex items-center gap-2 bg-gray-800 hover:bg-gray-900 text-white px-6 py-3 rounded-lg font-medium"
              >
                <StopIcon className="w-5 h-5" />
                Stop Recording
              </button>
            )}

            {audioBlob && !isRecording && (
              <>
                <button
                  onClick={playRecording}
                  className="flex items-center gap-2 bg-blue-500 hover:bg-blue-600 text-white px-4 py-3 rounded-lg"
                >
                  {isPlaying ? <PauseIcon className="w-5 h-5" /> : <PlayIcon className="w-5 h-5" />}
                </button>
                <button
                  onClick={deleteRecording}
                  className="flex items-center gap-2 bg-gray-500 hover:bg-gray-600 text-white px-4 py-3 rounded-lg"
                >
                  <TrashIcon className="w-5 h-5" />
                </button>
              </>
            )}
          </div>
        </div>

        {/* Processing Status */}
        {isProcessing && (
          <div className="text-center py-6">
            <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600 mx-auto mb-4"></div>
            <p className="text-gray-600">
              {recordingTime > 1800 
                ? 'Processing long recording... This may take several minutes for 45+ minute recordings.' 
                : 'Processing your voice recording...'}
            </p>
            {recordingTime > 2700 && ( // 45+ minutes
              <p className="text-sm text-amber-600 mt-2">
                Large meetings can take 3-5 minutes to process. Please be patient.
              </p>
            )}
          </div>
        )}

        {/* Error Message */}
        {error && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4">
            <div className="flex items-center gap-2">
              <ExclamationTriangleIcon className="w-5 h-5 text-red-500" />
              <span className="text-red-800">{error}</span>
            </div>
          </div>
        )}

        {/* Transcription */}
        {transcription && (
          <div className="space-y-4">
            <div>
              <h4 className="font-medium text-gray-900 mb-2">Transcription</h4>
              <div className="bg-gray-50 rounded-lg p-4">
                <p className="text-gray-700">{transcription}</p>
              </div>
            </div>

            {/* Extracted Notes */}
            {extractedNotes.length > 0 && (
              <div>
                <h4 className="font-medium text-gray-900 mb-3">Extracted Notes</h4>
                <div className="space-y-3">
                  {extractedNotes.map((note, index) => (
                    <div key={index} className="border border-gray-200 rounded-lg p-4">
                      <div className="flex items-start justify-between mb-2">
                        <div className="flex items-center gap-2">
                          {getNoteIcon(note.type)}
                          <span className="text-sm font-medium capitalize text-gray-900">
                            {note.type}
                          </span>
                        </div>
                        <div className="flex items-center gap-2">
                          <span className={`px-2 py-1 rounded-full text-xs font-medium border ${getPriorityColor(note.priority)}`}>
                            {note.priority}
                          </span>
                          <span className="text-xs text-gray-500">
                            {Math.round(note.confidence * 100)}%
                          </span>
                        </div>
                      </div>
                      <p className="text-gray-700 text-sm">{note.content}</p>
                    </div>
                  ))}
                </div>

                {/* Save Button */}
                <div className="flex justify-end gap-3 pt-4">
                  {onClose && (
                    <button onClick={onClose} className="btn-secondary">
                      Cancel
                    </button>
                  )}
                  <button
                    onClick={saveNotes}
                    disabled={isProcessing}
                    className="btn-primary disabled:opacity-50"
                  >
                    {isProcessing ? 'Saving...' : 'Save Notes'}
                  </button>
                </div>
              </div>
            )}
          </div>
        )}

        {/* Task Assignment Suggestions */}
        {showTaskSuggestions && (
          <TaskAssignmentSuggestions
            suggestions={taskSuggestions}
            onAssignToTask={handleAssignToTask}
            onCreateNewTask={handleCreateNewTask}
            onSkip={handleSkipAction}
            onClose={handleCloseSuggestions}
          />
        )}

        {/* Hidden audio element for playback */}
        <audio ref={audioRef} style={{ display: 'none' }} />
      </div>
    </div>
  );
};

export default VoiceRecorder;