import React, { useState } from 'react';
import { Task, User } from '../../types';
import { ActionItemSuggestion, TaskMatch } from '../../services/taskMatchingService';
import {
  CheckIcon,
  XMarkIcon,
  PlusIcon,
  ClockIcon,
  UserIcon,
  FlagIcon,
  ChevronRightIcon,
  ExclamationTriangleIcon
} from '@heroicons/react/24/outline';

interface TaskAssignmentSuggestionsProps {
  suggestions: ActionItemSuggestion[];
  onAssignToTask: (actionItemId: string, taskId: string, updates?: Partial<Task>) => void;
  onCreateNewTask: (actionItemId: string, taskTitle: string, taskData: Partial<Task>) => void;
  onSkip: (actionItemId: string) => void;
  onClose: () => void;
}

const TaskAssignmentSuggestions: React.FC<TaskAssignmentSuggestionsProps> = ({
  suggestions,
  onAssignToTask,
  onCreateNewTask,
  onSkip,
  onClose
}) => {
  const [expandedSuggestion, setExpandedSuggestion] = useState<number | null>(0);

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high': return 'bg-red-100 text-red-800 border-red-200';
      case 'medium': return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      case 'low': return 'bg-green-100 text-green-800 border-green-200';
      default: return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  const getMatchScoreColor = (score: number) => {
    if (score >= 0.8) return 'text-green-600 bg-green-50';
    if (score >= 0.6) return 'text-yellow-600 bg-yellow-50';
    return 'text-red-600 bg-red-50';
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      weekday: 'short',
      month: 'short',
      day: 'numeric'
    });
  };

  if (suggestions.length === 0) {
    return null;
  }

  return (
    <div className="fixed inset-0 bg-gray-500 bg-opacity-75 flex items-center justify-center p-4 z-50">
      <div className="bg-white rounded-lg shadow-xl max-w-4xl w-full max-h-[90vh] overflow-hidden">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b">
          <div>
            <h2 className="text-xl font-semibold text-gray-900 flex items-center gap-2">
              <CheckIcon className="w-6 h-6 text-primary-600" />
              Smart Task Assignment
            </h2>
            <p className="text-sm text-gray-600 mt-1">
              AI found {suggestions.length} action item{suggestions.length !== 1 ? 's' : ''} that can be assigned to tasks
            </p>
          </div>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600"
          >
            <XMarkIcon className="w-6 h-6" />
          </button>
        </div>

        {/* Content */}
        <div className="overflow-y-auto max-h-[70vh]">
          <div className="p-6 space-y-6">
            {suggestions.map((suggestion, index) => (
              <div key={index} className="border border-gray-200 rounded-lg overflow-hidden">
                {/* Action Item Header */}
                <div className="bg-gray-50 p-4 border-b">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <h3 className="font-medium text-gray-900 mb-2">
                        Action Item {index + 1}
                      </h3>
                      <p className="text-sm text-gray-700 bg-white p-3 rounded border">
                        "{suggestion.actionItem.content}"
                      </p>
                      <div className="flex items-center gap-4 mt-2 text-xs text-gray-500">
                        <span className={`px-2 py-1 rounded-full border ${getPriorityColor(suggestion.actionItem.priority)}`}>
                          {suggestion.actionItem.priority} priority
                        </span>
                        <span>{Math.round(suggestion.actionItem.confidence * 100)}% confidence</span>
                      </div>
                    </div>
                    <button
                      onClick={() => setExpandedSuggestion(expandedSuggestion === index ? null : index)}
                      className="ml-4 p-2 hover:bg-gray-100 rounded"
                    >
                      <ChevronRightIcon 
                        className={`w-5 h-5 transition-transform ${
                          expandedSuggestion === index ? 'rotate-90' : ''
                        }`} 
                      />
                    </button>
                  </div>
                </div>

                {/* Suggestions Content */}
                {expandedSuggestion === index && (
                  <div className="p-4">
                    {/* Existing Task Matches */}
                    {suggestion.taskMatches.length > 0 && (
                      <div className="mb-6">
                        <h4 className="font-medium text-gray-900 mb-3 flex items-center gap-2">
                          <FlagIcon className="w-4 h-4" />
                          Suggested Existing Tasks
                        </h4>
                        <div className="space-y-3">
                          {suggestion.taskMatches.slice(0, 3).map((match, matchIndex) => (
                            <TaskMatchCard 
                              key={matchIndex}
                              match={match}
                              onAssign={() => {
                                const updates: Partial<Task> = {};
                                if (match.suggestedAssignee) {
                                  updates.assignee_id = match.suggestedAssignee.id;
                                }
                                if (match.suggestedDeadline) {
                                  updates.deadline = match.suggestedDeadline;
                                }
                                if (match.suggestedPriority) {
                                  updates.priority = match.suggestedPriority;
                                }
                                onAssignToTask(suggestion.actionItem.timestamp, match.task.id, updates);
                              }}
                            />
                          ))}
                        </div>
                      </div>
                    )}

                    {/* Create New Task Option */}
                    {suggestion.suggestCreateNew && (
                      <div className="mb-6">
                        <h4 className="font-medium text-gray-900 mb-3 flex items-center gap-2">
                          <PlusIcon className="w-4 h-4" />
                          Create New Task
                        </h4>
                        <CreateNewTaskCard
                          suggestion={suggestion}
                          onCreateNew={(taskData) => 
                            onCreateNewTask(
                              suggestion.actionItem.timestamp,
                              suggestion.suggestedTaskTitle || 'New Task',
                              taskData
                            )
                          }
                        />
                      </div>
                    )}

                    {/* Action Buttons */}
                    <div className="flex justify-end gap-3 pt-4 border-t">
                      <button
                        onClick={() => onSkip(suggestion.actionItem.timestamp)}
                        className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 hover:bg-gray-200 rounded-md transition-colors"
                      >
                        Skip This Action
                      </button>
                    </div>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>

        {/* Footer */}
        <div className="flex justify-between items-center p-6 border-t bg-gray-50">
          <p className="text-sm text-gray-600">
            Review and assign action items to keep your project organized
          </p>
          <button
            onClick={onClose}
            className="btn-secondary"
          >
            Done
          </button>
        </div>
      </div>
    </div>
  );
};

// Component for individual task match
const TaskMatchCard: React.FC<{ match: TaskMatch; onAssign: () => void }> = ({ match, onAssign }) => {
  const getMatchScoreColor = (score: number) => {
    if (score >= 0.8) return 'text-green-600 bg-green-50';
    if (score >= 0.6) return 'text-yellow-600 bg-yellow-50';
    return 'text-red-600 bg-red-50';
  };

  return (
    <div className="border border-gray-200 rounded-lg p-4 hover:bg-gray-50 transition-colors">
      <div className="flex items-start justify-between mb-3">
        <div className="flex-1">
          <h5 className="font-medium text-gray-900">{match.task.title}</h5>
          {match.task.description && (
            <p className="text-sm text-gray-600 mt-1">{match.task.description}</p>
          )}
        </div>
        <div className={`px-2 py-1 rounded text-xs font-medium ${getMatchScoreColor(match.score)}`}>
          {Math.round(match.score * 100)}% match
        </div>
      </div>

      {/* Match Reasons */}
      <div className="mb-3">
        <p className="text-xs font-medium text-gray-500 mb-1">Why this matches:</p>
        <div className="flex flex-wrap gap-1">
          {match.matchReasons.map((reason, index) => (
            <span key={index} className="text-xs bg-blue-100 text-blue-800 px-2 py-1 rounded">
              {reason}
            </span>
          ))}
        </div>
      </div>

      {/* Suggested Updates */}
      <div className="flex items-center gap-4 mb-3 text-xs text-gray-600">
        {match.suggestedAssignee && (
          <span className="flex items-center gap-1">
            <UserIcon className="w-3 h-3" />
            Assign to: {match.suggestedAssignee.full_name}
          </span>
        )}
        {match.suggestedDeadline && (
          <span className="flex items-center gap-1">
            <ClockIcon className="w-3 h-3" />
            Due: {new Date(match.suggestedDeadline).toLocaleDateString()}
          </span>
        )}
        {match.suggestedPriority && (
          <span className="flex items-center gap-1">
            <ExclamationTriangleIcon className="w-3 h-3" />
            Priority: {match.suggestedPriority}
          </span>
        )}
      </div>

      <button
        onClick={onAssign}
        className="w-full btn-primary text-sm"
      >
        Assign to This Task
      </button>
    </div>
  );
};

// Component for creating new task
const CreateNewTaskCard: React.FC<{ 
  suggestion: ActionItemSuggestion; 
  onCreateNew: (taskData: Partial<Task>) => void;
}> = ({ suggestion, onCreateNew }) => {
  const handleCreateNew = () => {
    const taskData: Partial<Task> = {
      title: suggestion.suggestedTaskTitle || 'New Task from Meeting',
      description: `Action item from meeting: "${suggestion.actionItem.content}"`,
      priority: suggestion.actionItem.priority,
      status: 'todo' as const
    };

    onCreateNew(taskData);
  };

  return (
    <div className="border border-dashed border-gray-300 rounded-lg p-4 bg-gray-50">
      <div className="text-center">
        <PlusIcon className="w-8 h-8 text-gray-400 mx-auto mb-2" />
        <h5 className="font-medium text-gray-900 mb-1">
          "{suggestion.suggestedTaskTitle}"
        </h5>
        <p className="text-sm text-gray-600 mb-3">
          No existing task matches well. Create a new task for this action item.
        </p>
        <button
          onClick={handleCreateNew}
          className="btn-primary text-sm"
        >
          Create New Task
        </button>
      </div>
    </div>
  );
};

export default TaskAssignmentSuggestions;