import { Task, User } from '../types';
import { VoiceNote } from './voiceService';

export interface TaskMatch {
  task: Task;
  score: number;
  matchReasons: string[];
  suggestedAssignee?: User;
  suggestedDeadline?: string;
  suggestedPriority?: 'low' | 'medium' | 'high';
}

export interface ActionItemSuggestion {
  actionItem: VoiceNote;
  taskMatches: TaskMatch[];
  suggestCreateNew: boolean;
  suggestedTaskTitle?: string;
}

class TaskMatchingService {
  // Main function to analyze voice notes and suggest task assignments
  analyzeAndSuggestAssignments(
    voiceNotes: VoiceNote[],
    projectTasks: Task[],
    teamMembers: User[]
  ): ActionItemSuggestion[] {
    console.log('TaskMatchingService.analyzeAndSuggestAssignments called with:', {
      voiceNotesCount: voiceNotes.length,
      projectTasksCount: projectTasks.length,
      teamMembersCount: teamMembers.length,
      voiceNotes: voiceNotes.map(n => ({ type: n.type, content: n.content.substring(0, 50) + '...' })),
      projectTasks: projectTasks.map(t => ({ id: t.id, title: t.title }))
    });
    
    const actionItems = voiceNotes.filter(note => note.type === 'action');
    console.log('Found action items:', actionItems.length);
    
    const suggestions = actionItems.map(actionItem => {
      const taskMatches = this.findTaskMatches(actionItem, projectTasks, teamMembers);
      const suggestCreateNew = this.shouldSuggestNewTask(actionItem, projectTasks);
      const suggestedTaskTitle = this.generateTaskTitle(actionItem);
      
      console.log('Generated suggestion for action item:', {
        content: actionItem.content.substring(0, 50) + '...',
        taskMatchesCount: taskMatches.length,
        suggestCreateNew,
        suggestedTaskTitle
      });
      
      return {
        actionItem,
        taskMatches,
        suggestCreateNew,
        suggestedTaskTitle
      };
    });
    
    console.log('Total suggestions generated:', suggestions.length);
    return suggestions;
  }

  // Find matching tasks for an action item
  private findTaskMatches(
    actionItem: VoiceNote,
    tasks: Task[],
    teamMembers: User[]
  ): TaskMatch[] {
    const matches: TaskMatch[] = [];

    for (const task of tasks) {
      const score = this.calculateMatchScore(actionItem, task);
      if (score > 0.3) { // Only include matches above 30% confidence
        const matchReasons = this.getMatchReasons(actionItem, task);
        const suggestedAssignee = this.extractAssignee(actionItem, teamMembers);
        const suggestedDeadline = this.extractDeadline(actionItem);
        const suggestedPriority = this.extractPriority(actionItem);

        matches.push({
          task,
          score,
          matchReasons,
          suggestedAssignee,
          suggestedDeadline,
          suggestedPriority
        });
      }
    }

    // Sort by match score (highest first)
    return matches.sort((a, b) => b.score - a.score);
  }

  // Calculate how well an action item matches a task
  private calculateMatchScore(actionItem: VoiceNote, task: Task): number {
    const actionText = actionItem.content.toLowerCase();
    const taskTitle = task.title.toLowerCase();
    const taskDescription = (task.description || '').toLowerCase();

    let score = 0;

    // Keyword matching
    const keywords = this.extractKeywords(actionText);
    for (const keyword of keywords) {
      if (taskTitle.includes(keyword)) {
        score += 0.3; // High weight for title matches
      }
      if (taskDescription.includes(keyword)) {
        score += 0.2; // Medium weight for description matches
      }
    }

    // Technology/feature matching
    const techKeywords = this.extractTechKeywords(actionText);
    for (const tech of techKeywords) {
      if (taskTitle.includes(tech) || taskDescription.includes(tech)) {
        score += 0.25;
      }
    }

    // Status/workflow matching
    if (this.hasWorkflowKeywords(actionText, task)) {
      score += 0.15;
    }

    // Assignee matching (if task has assignee and action mentions them)
    if (task.assignee && this.mentionsAssignee(actionText, task.assignee)) {
      score += 0.2;
    }

    return Math.min(score, 1.0); // Cap at 100%
  }

  // Extract meaningful keywords from action item
  private extractKeywords(text: string): string[] {
    // Remove common words and extract meaningful terms
    const stopWords = ['the', 'and', 'or', 'but', 'in', 'on', 'at', 'to', 'for', 'of', 'with', 'by', 'is', 'are', 'was', 'were', 'be', 'been', 'being', 'have', 'has', 'had', 'do', 'does', 'did', 'will', 'would', 'could', 'should', 'may', 'might', 'must', 'can', 'need', 'needs', 'want', 'wants'];
    
    const words = text.toLowerCase()
      .replace(/[^\w\s]/g, ' ')
      .split(/\s+/)
      .filter(word => word.length > 2 && !stopWords.includes(word));

    return Array.from(new Set(words)); // Remove duplicates
  }

  // Extract technology/feature keywords
  private extractTechKeywords(text: string): string[] {
    const techTerms = [
      'login', 'authentication', 'auth', 'signup', 'register',
      'dashboard', 'ui', 'interface', 'design', 'frontend', 'backend',
      'api', 'database', 'db', 'server', 'client',
      'testing', 'test', 'qa', 'bug', 'fix',
      'deployment', 'deploy', 'production', 'staging',
      'user', 'admin', 'profile', 'settings',
      'notification', 'email', 'sms', 'alert',
      'search', 'filter', 'sort', 'pagination',
      'security', 'permission', 'role', 'access',
      'integration', 'api', 'webhook', 'sync'
    ];

    return techTerms.filter(term => text.toLowerCase().includes(term));
  }

  // Check for workflow-related keywords
  private hasWorkflowKeywords(text: string, task: Task): boolean {
    const statusKeywords: Record<string, string[]> = {
      'todo': ['start', 'begin', 'initiate', 'kick off'],
      'in_progress': ['continue', 'working on', 'in progress', 'ongoing'],
      'review': ['review', 'check', 'validate', 'approve', 'test'],
      'done': ['complete', 'finish', 'done', 'finalize', 'deploy']
    };

    const keywords = statusKeywords[task.status] || [];
    return keywords.some(keyword => text.toLowerCase().includes(keyword));
  }

  // Check if action item mentions the task assignee
  private mentionsAssignee(text: string, assignee: any): boolean {
    if (!assignee || !assignee.full_name) return false;
    
    const names = assignee.full_name.toLowerCase().split(' ');
    return names.some((name: string) => text.toLowerCase().includes(name));
  }

  // Extract assignee from action item text
  private extractAssignee(actionItem: VoiceNote, teamMembers: User[]): User | undefined {
    const text = actionItem.content.toLowerCase();
    
    for (const member of teamMembers) {
      if (member.full_name) {
        const names = member.full_name.toLowerCase().split(' ');
        if (names.some((name: string) => text.includes(name))) {
          return member;
        }
      }
    }
    
    return undefined;
  }

  // Extract deadline from action item text
  private extractDeadline(actionItem: VoiceNote): string | undefined {
    const text = actionItem.content.toLowerCase();
    const today = new Date();
    
    // Check for specific deadline patterns
    let match: RegExpMatchArray | null;
    
    // Check for weekday deadlines
    match = text.match(/by (monday|tuesday|wednesday|thursday|friday|saturday|sunday)/i);
    if (match) {
      const days = this.getDaysUntilWeekday(match[1]);
      const deadline = new Date(today);
      deadline.setDate(deadline.getDate() + days);
      return deadline.toISOString().split('T')[0];
    }
    
    // Check for "by friday" specifically
    if (text.includes('by friday')) {
      const days = this.getDaysUntilWeekday('friday');
      const deadline = new Date(today);
      deadline.setDate(deadline.getDate() + days);
      return deadline.toISOString().split('T')[0];
    }
    
    // Check for "by next week"
    if (text.includes('by next week')) {
      const deadline = new Date(today);
      deadline.setDate(deadline.getDate() + 7);
      return deadline.toISOString().split('T')[0];
    }
    
    // Check for "by end of week"
    if (text.includes('by end of week')) {
      const days = this.getDaysUntilWeekday('friday');
      const deadline = new Date(today);
      deadline.setDate(deadline.getDate() + days);
      return deadline.toISOString().split('T')[0];
    }
    
    // Check for "tomorrow"
    if (text.includes('tomorrow')) {
      const deadline = new Date(today);
      deadline.setDate(deadline.getDate() + 1);
      return deadline.toISOString().split('T')[0];
    }
    
    // Check for "today"
    if (text.includes('today')) {
      return today.toISOString().split('T')[0];
    }
    
    // Check for specific dates (by 15th, by 20th, etc.)
    match = text.match(/by (\d+)(?:st|nd|rd|th)?/i);
    if (match) {
      const day = parseInt(match[1]);
      const nextMonth = new Date(today.getFullYear(), today.getMonth() + 1, day);
      return nextMonth.toISOString().split('T')[0];
    }

    return undefined;
  }

  // Get days until specific weekday
  private getDaysUntilWeekday(weekday: string): number {
    const weekdays = ['sunday', 'monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday'];
    const today = new Date();
    const targetDay = weekdays.indexOf(weekday.toLowerCase());
    const currentDay = today.getDay();
    
    let daysUntil = targetDay - currentDay;
    if (daysUntil <= 0) daysUntil += 7; // Next week if day has passed
    
    return daysUntil;
  }

  // Extract priority from action item text
  private extractPriority(actionItem: VoiceNote): 'low' | 'medium' | 'high' | undefined {
    const text = actionItem.content.toLowerCase();
    
    if (text.includes('urgent') || text.includes('asap') || text.includes('critical') || text.includes('immediately')) {
      return 'high';
    }
    if (text.includes('important') || text.includes('priority') || text.includes('soon')) {
      return 'medium';
    }
    
    return actionItem.priority; // Default to voice note priority
  }

  // Get reasons why a task matches
  private getMatchReasons(actionItem: VoiceNote, task: Task): string[] {
    const reasons: string[] = [];
    const actionText = actionItem.content.toLowerCase();
    const taskTitle = task.title.toLowerCase();
    const taskDescription = (task.description || '').toLowerCase();

    // Check for keyword matches
    const keywords = this.extractKeywords(actionText);
    const titleMatches = keywords.filter(keyword => taskTitle.includes(keyword));
    const descMatches = keywords.filter(keyword => taskDescription.includes(keyword));

    if (titleMatches.length > 0) {
      reasons.push(`Title keywords: ${titleMatches.join(', ')}`);
    }
    if (descMatches.length > 0) {
      reasons.push(`Description keywords: ${descMatches.join(', ')}`);
    }

    // Check for tech keywords
    const techKeywords = this.extractTechKeywords(actionText);
    const techMatches = techKeywords.filter(tech => 
      taskTitle.includes(tech) || taskDescription.includes(tech)
    );
    if (techMatches.length > 0) {
      reasons.push(`Technology: ${techMatches.join(', ')}`);
    }

    // Check assignee match
    if (task.assignee && this.mentionsAssignee(actionText, task.assignee)) {
      reasons.push(`Mentions assignee: ${task.assignee.full_name}`);
    }

    return reasons;
  }

  // Determine if we should suggest creating a new task
  private shouldSuggestNewTask(actionItem: VoiceNote, tasks: Task[]): boolean {
    const matches = this.findTaskMatches(actionItem, tasks, []);
    const bestMatch = matches[0];
    
    // Suggest new task if no good matches found (score < 60%)
    return !bestMatch || bestMatch.score < 0.6;
  }

  // Generate a suggested task title from action item
  private generateTaskTitle(actionItem: VoiceNote): string {
    const text = actionItem.content;
    
    // Clean up the text and create a title
    let title = text
      .replace(/^(john|sarah|mike|team|we|he|she|they)\s+/i, '') // Remove names at start
      .replace(/\s+(by|before|until)\s+.*/i, '') // Remove deadline parts
      .replace(/^(need to|should|must|will|needs to)\s+/i, '') // Remove action words
      .trim();

    // Capitalize first letter
    title = title.charAt(0).toUpperCase() + title.slice(1);

    // Limit length
    if (title.length > 50) {
      title = title.substring(0, 47) + '...';
    }

    return title || 'New Task from Meeting';
  }
}

export const taskMatchingService = new TaskMatchingService();
export default taskMatchingService;